.bottom-right-table-1 {
  width: calc(25% + 10px);
  height: 100%;
  margin: 0 -5px;
}
.bottom-right-table-1 .border-box-content {
  padding: 20px;
  box-sizing: border-box;
}
.bottom-right-table-1 .table-name {
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
}
.bottom-right-table-1 .table-name img {
  width: 40px;
  height: 40px;
  margin-right: 5px;
}
.bottom-right-table-1 .dv-scroll-board {
  height: calc(100% - 60px);
}
