.bottom-left-chart-1 {
  position: relative;
}
.bottom-left-chart-1 .header-name {
  height: 150px;
  line-height: 150px;
  font-size: 20px;
  text-align: center;
}
.bottom-left-chart-1 .details-value {
  height: 40px;
  font-size: 30px;
  font-weight: bold;
  text-align: center;
}
.bottom-left-chart-1 .details-value span {
  color: #00c0ff;
  font-size: 45px;
}
.bottom-left-chart-1 .dv-charts-container {
  height: calc(100% - 190px);
}
.bottom-left-chart-1 .decoration-ring {
  position: absolute;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 5px solid rgba(254, 254, 254, 0.3);
  top: 190px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
