.top-left-cmp {
  display: flex;
}
.top-left-cmp .dc-left,
.top-left-cmp .dc-right {
  width: 50%;
}
.top-left-cmp .dv-border-box-5 {
  height: 60%;
}
.top-left-cmp .dc-text {
  display: flex;
  flex-direction: column;
  height: 40%;
  font-size: 20px;
  padding: 20px;
  box-sizing: border-box;
}
.top-left-cmp .dc-left .dc-text {
  align-items: flex-end;
  justify-content: center;
}
.top-left-cmp .dc-right .dc-text {
  justify-content: flex-start;
  padding-top: 20px;
}
.top-left-cmp .dc-left .dv-border-box-5 {
  padding: 30px;
  box-sizing: border-box;
}
.top-left-cmp .dc-right .dv-border-box-5 {
  padding: 40px;
  padding-left: 75px;
  box-sizing: border-box;
}
.top-left-cmp .main-value {
  font-weight: bold;
  font-size: 30px;
}
.top-left-cmp .main-value span {
  font-size: 50px;
  color: #00c0ff;
  margin-right: 15px;
}
.top-left-cmp .compare-value {
  height: 35px;
  line-height: 35px;
  font-size: 18px;
}
.top-left-cmp .compare-value span {
  margin-right: 30px;
}
